input {
    text-transform: none;
}

input[type='number'] {
    -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 40px 50px 0 50px;
    padding: 50px;
    background-color: var(--accent-color);
    border-radius: 10px;
}

.title {
    font-size: 40px;
    margin-bottom: 20px;
}

.subTitle {
    font-size: 25px;
}